import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DriversListComponent } from './drivers-list.component';
import { SharedModule } from '../shared/shared.module';
import { DriverService } from '../services/driver.service';
import { driversListRouting } from './drivers-list.routing';
import { DriversFormDialogModule } from '../drivers-form-dialog/drivers-form-dialog.module';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    driversListRouting,
    DriversFormDialogModule
  ],
  declarations: [DriversListComponent],
  providers: [
    DriverService
  ]
})
export class DriversListModule { }



