import { RouterModule, Routes } from '@angular/router';
import { DriversListComponent } from './drivers-list.component';
import { ModuleWithProviders } from '@angular/core';

const driversListRoutes: Routes = [
    {
        path: '', component: DriversListComponent
    }
];

export const driversListRouting: ModuleWithProviders = RouterModule.forChild(driversListRoutes);



