import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DriversFormDialogComponent } from './drivers-form-dialog.component';
import { DriversFormModule } from '../drivers-form/drivers-form.module';
import { MatDialogModule } from '@angular/material';

@NgModule({
  imports: [
    CommonModule,
    DriversFormModule,
    MatDialogModule
  ],
  declarations: [
    DriversFormDialogComponent
  ],
  exports: [
    DriversFormDialogComponent
  ],
  entryComponents: [
    DriversFormDialogComponent
  ]
})
export class DriversFormDialogModule { }



